}

.typewriter {
    display: inline;
    font-size: inherit;
    white-space: pre-wrap;
}

.ios-chat {
    border: 2px solid black;
    border-radius: 1.5em;

    background: linear-gradient(#ACC8EB, white);
    padding: .5em .8em;

}
.typewriter:after {
    content: "|";
    animation: blinky 1.5s ease-in-out 0s infinite;
}

@keyframes blinky {
    0% { opacity : 0 }
    50% { opacity: 1 }
    100%{ opacity: 0 }
}
